@mixin absCenter {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) !important;
}

@mixin verticalCenter {
    position: absolute;
    top: 50%;
    transform: translate(0%, -50%);
} 

