.gutter-example .ant-row > div {
    background: transparent;
    border: 0;
}
.blockexplorer{
    &__blockTransaction{
        // margin:2rem 0;
        margin-bottom: 7rem;
        &-col{
            margin-bottom:2rem;
        }
    }

    &__chartrow{
        margin-bottom: 7rem;        
    }
    .ant-table-tbody > tr > th{
        white-space: nowrap;
    }
    .ant-table-tbody > tr > td{
        // cursor: pointer;
    }
    .ant-table-tbody > tr > td.txnoverflow{
        max-width: 10rem;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        // &:hover{

        // }
    }
    


    @media screen  and (max-width:768px){
        .ant-spin-container{
            margin: 1rem;
        }
        .ant-table-thead > tr > th.mobilehide{
            display: none;
        }
        .ant-table-tbody > tr > td.mobilehide{
            display: none;
        }
        .ant-table-thead > tr > th{
       
            // min-width: 9rem;
        }
        .ant-table-tbody > tr > td{
         
        }
    }
    @media screen  and (max-width:992px){
        .ant-table-thead > tr > th{
          padding-left: 0;
          padding-right: 0;
        }
    }


    &__content{
        background-color: #fff;
        margin: 2rem;
        padding: 2rem;
        min-height: 32rem;
    }
   
