.blocks-details-list{

    @media screen and (max-width:768px) {
        .ant-table-thead > tr > th.mobilehide{
            display: none;
        }
        .ant-table-tbody > tr > td.mobilehide{
            display: none;
        }
    }
    &__headercol{
        border: 1px solid #ddd;
        padding: 0.4rem;
        text-transform: capitalize;
        
        &--value{    
            overflow: hidden;
            text-overflow: ellipsis;
        }
       &--certificate{
            // height: 18rem;
            height: 4.5rem;
       }
    }
    &__metaInfo{
        margin-bottom: 1.5rem;
    }
    &__MetaInfoTable{
        .ant-table-tbody > tr > td:first-child{
            width:6rem;
        }
       
    }
    .loader{
        top:50%;
    }
    &__listTable{
        .ant-table-tbody > tr >td{
            cursor: pointer;
        }
    }
}